
.movie{
    padding : 0 10px;
    border:1px solid gray;
    border-radius: 5px;
    width: 45%;
    height: 250px;
    display: flex;
    flex-direction: row;
    margin-bottom: 100px;
    background-color: white;
    box-shadow:10px 5px 5px #adaeb9;
  }
.movie img{
    position: relative;
    width: 150px;
    height: 180px;
    top:-30px;
}
.movie .movie_data{
    margin-left : 10px;
}
.movie .movie_data .movie_title{
    font-size: 1.2em;
    margin-bottom: 20px;
    height: 20%;
}
.movie .movie_data .movie_year,.genres,.movie_summary{
    font-size: 0.7em;
    margin-bottom: 5px;
    color: #adaeb9;
}
.movie .movie_data .genres li{
    margin-right:5px
}
