body{
    background-color:azure;
    margin : 0;
    padding: 0;
    min-width: 40vh;
    min-height:1000px;
}
li{
    list-style: none;
}
.loader{
    text-align: center;
}

.container .movies{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content:space-between;
    margin-top : 50px;
    padding-left: 10%;
    padding-right: 10%;
}

.movie{
    padding : 0 10px;
    border:1px solid gray;
    border-radius: 5px;
    width: 45%;
    height: 250px;
    display: flex;
    flex-direction: row;
    margin-bottom: 100px;
    background-color: white;
    box-shadow:10px 5px 5px #adaeb9;
  }

.movie img{
    position: relative;
    width: 150px;
    height: 180px;
    top:-30px;
}
.movie .movie_data{
    margin-left : 10px;
}
.movie .movie_data .movie_title{
    font-size: 1.2em;
}
.movie .movie_data .movie_year,.genres,.movie_summary{
    font-size: 0.7em;
    margin-top: 10px;
    color: #adaeb9;
}
.movie .movie_data .genres{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding:0;
}
.movie .movie_data .genres li{
    margin-right:5px
}
.movie .movie_data .movie_summary{
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3; 
    -webkit-box-orient: vertical;
}
@media screen and (max-width:600px){
    .container .movies{
        flex-direction: column;
        justify-content:center;
    }
    .movie{
        width: 95%;
    }
}